/* 💳 Funds section */
.funds-container {
  padding: 2rem;
  background: #f8f9fb;
  font-family: 'Segoe UI', sans-serif;
}

.funds-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 1rem 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  margin-bottom: 2rem;
}

.funds-header p {
  font-size: 1.2rem;
  font-weight: bold;
}

.funds-actions {
  display: flex;
  gap: 1rem;
}

.amount-input {
  padding: 0.5rem 0.8rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 0.95rem;
}

.btn {
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  color: white;
  transition: background 0.3s ease;
}

.add-btn {
  background-color: #4caf50;
}

.withdraw-btn {
  background-color: #007bff;
}

.funds-summary {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
}

.funds-summary h2 {
  margin-bottom: 1.5rem;
  color: #222;
}

.funds-table {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.funds-row {
  display: flex;
  justify-content: space-between;
  font-size: 0.95rem;
  color: #444;
}

.highlight {
  font-weight: bold;
}

.highlight.positive {
  color: green;
}

.loading-msg {
  font-size: 1.2rem;
  text-align: center;
  margin-top: 2rem;
}

/* Transactions */
.transactions-section {
  background: white;
  padding: 1.5rem;
  margin-top: 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
}

.transactions-section h3 {
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.transactions-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.95rem;
}

.transactions-table th,
.transactions-table td {
  padding: 0.75rem;
  border-bottom: 1px solid #eee;
  text-align: left;
}

.txn-type {
  text-transform: capitalize;
  font-weight: bold;
}

.txn-type.add {
  color: green;
}
.txn-type.withdraw {
  color: red;
}
.txn-type.buy {
  color: orange;
}
.txn-type.sell {
  color: #007bff;
}



